import React, { useState } from 'react';
import { Table, Button, Modal, Form, Input, InputNumber, Popconfirm, Space, Row, Col } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined } from '@ant-design/icons';

const CustomTable = () => {
  
  const [records, setRecords] = useState([]);
  const [selectedRecord, setSelectedRecord] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const [form] = Form.useForm();

  const showAddModal = () => {
    setSelectedRecord(null);
    setIsModalOpen(true);
    form.resetFields();
  };

  const showEditModal = (record) => {
    setSelectedRecord(record);
    setIsModalOpen(true);
    form.setFieldsValue(record);
  };

  const deleteRecord = (key) => {
    setRecords(records.filter((item) => item.key !== key));
  };

  const handleModalSubmit = () => {
    form
      .validateFields()
      .then((values) => {
        if (selectedRecord) {
          setRecords((prevRecords) =>
            prevRecords.map((item) =>
              item.key === selectedRecord.key ? { ...item, ...values } : item
            )
          );
        } else {
          setRecords((prevRecords) => [
            ...prevRecords,
            { key: Date.now(), ...values },
          ]);
        }
        setIsModalOpen(false);
      })
      .catch((error) => {
        console.log(':', error);
      });
  };

  const columns = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      sorter: (a, b) => a.name.localeCompare(b.name),
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 10 }}>
          <Input
            placeholder="  "
            value={selectedKeys[0]}
            onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={confirm}
            style={{ marginBottom: 8, display: 'block' }}
          />
          <Space>
            <Button
              type="primary"
              onClick={confirm}
              size="small"
              style={{ width: 100 }}
            >
              
            </Button>
            <Button onClick={clearFilters} size="small" style={{ width: 100 }}>
              
            </Button>
          </Space>
        </div>
      ),
      onFilter: (value, record) => record.name.toLowerCase().includes(value.toLowerCase()),
    },
    {
      title: '',
      dataIndex: 'age',
      key: 'age',
      sorter: (a, b) => a.age - b.age,
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 10 }}>
          <InputNumber
            placeholder="  "
            value={selectedKeys[0]}
            onChange={(value) => setSelectedKeys(value ? [String(value)] : [])}
            onPressEnter={confirm}
            style={{ marginBottom: 8, width: '100%' }}
          />
          <Space>
            <Button
              type="primary"
              onClick={confirm}
              size="small"
              style={{ width: 100 }}
            >
              
            </Button>
            <Button onClick={clearFilters} size="small" style={{ width: 100 }}>
              
            </Button>
          </Space>
        </div>
      ),
      onFilter: (value, record) => record.age.toString().includes(value),
    },
    {
      title: '',
      key: 'actions',
      render: (_, record) => (
        <Space size="middle">
          <Button
            icon={<EditOutlined />}
            onClick={() => showEditModal(record)}
          />
          <Popconfirm
            title=" ?"
            onConfirm={() => deleteRecord(record.key)}
            okText=""
            cancelText=""
          >
            <Button icon={<DeleteOutlined />} danger />
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <div>
      <Row justify="space-between" align="middle" style={{ marginBottom: 15 }}>
        <Col>
          <Button
            type="primary"
            onClick={showAddModal}
            icon={<PlusCircleOutlined />}
          >
             
          </Button>
        </Col>
      </Row>
      <Table
        dataSource={records}
        columns={columns}
        rowKey="key"
        pagination={{ pageSize: 5 }}
      />
      <Modal
        title={selectedRecord ? '' : '  '}
        visible={isModalOpen}
        onOk={handleModalSubmit}
        onCancel={() => setIsModalOpen(false)}
        okText=""
        cancelText=""
      >
        <Form form={form} layout="vertical">
          <Form.Item
            name="name"
            label=""
            rules={[{ required: true, message: ' ' }]}
          >
            <Input placeholder="  " />
          </Form.Item>
          <Form.Item
            name="age"
            label=""
            rules={[{ required: true, message: ' ' }]}
          >
            <InputNumber min={1} max={100} style={{ width: '100%' }} placeholder=" " />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default CustomTable;
